html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

root {
  --pale-blue: #597CA6;
  --brown: #A68359;
}

@font-face {
  font-family: "Timeburner";
  src: url("../fonts/Timeburner-xJB8.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Arcade Classic";
  src: url(/design/ARCADECLASSIC.TTF);
}
@font-face {
  font-family: "Yoster Island";
  src: url(/design/yoster.ttf);
}
@font-face {
  font-family: "Cotham Sans";
  src: url(/design/CothamSans.otf);
}
@font-face {
  font-family: "Digitalt";
  src: url(/design/Digitalt.ttf);
}
@font-face {
