}

.nav-wrap {
  overflow: hidden;
  height: 100%;
  width: 27.1875rem;
  z-index: 30;
  position: absolute;
}

.nav-left {
  width: 2.1875rem;
  height: 2.1875rem;
  background-color: #A68359;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-13.0625rem);
  z-index: 30;
}

.nav-left:hover {
  background-color: #b59875;
}

.nav-left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.nav--label--table {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-right {
  width: 21.875rem;
  height: 2.1875rem;
  background-color: #7491b4;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
