  align-items: center;
  justify-content: center;
  z-index: 30;
}

.nav--label {
  padding: 0.46875rem 1.28125rem;
  text-align: center;
  color: #000;
  font-family: "Timeburner", sans-serif;
  font-size: 0.9375rem;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  text-decoration: none;
}

.nav--label:hover {
  text-decoration: underline;
  line-height: 138%;
  background-color: #89a2c0;
  height: 100%;
}

.card {
  padding: 1.875rem;
}

