}

.side-table--statement-intro-container {
  position: relative;
  display: inline-block;
  width: 60%;
  left: 50%;
  transform: translateX(-50%) translateY(9.375rem);
  text-align: center;
}
@media (max-width: 46.875em) {
  .side-table--statement-intro-container {
    font-size: 8vw;
    width: 70%;
  }
}

.side-table--statement-intro-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 1rem;
  filter: blur(1.875rem);
  z-index: 1;
  -webkit-mask-image: radial-gradient(circle at center, white 0%, transparent 100%);
  mask-image: radial-gradient(circle at center, white 0%, transparent 100%);
}

.side-table--statement-intro-text {
  position: relative;
  display: block;
  text-align: center;
  font-style: normal;
  font-size: 2vh;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 1rem;
  margin: 0 auto;
  border-radius: 1.875rem;
  backdrop-filter: blur(10px);
  -webkit-mask-image: radial-gradient(circle at center, rgb(0, 0, 0) 60%, rgba(0, 0, 0, 0) 100%);
  mask-image: radial-gradient(circle at center, rgb(0, 0, 0) 60%, rgba(0, 0, 0, 0) 100%);
}
.fullscreen-gradient {
  position: relative;
  justify-content: space-between;
  height: 25rem;
}

.non-scrolling-words-box {
  text-align: center;
  font-weight: 600;
  white-space: nowrap;
  display: inline-block;
  vertical-align: middle;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-left: 5%;
}

.scrolling-words-container {
  display: inline-block;
  vertical-align: middle;
  height: 8vh;
  align-items: center;
  padding: 0.625rem 1.875rem;
  font-size: 6vw;
  font-weight: 600;
  overflow: hidden;
  gap: 1vw;
  margin-left: -1.875rem;
  position: relative;
  border: 2px solid red;
}

.scrolling-words-box {
  height: 11vh;
  margin: auto;
  transform: translateY(3.125rem);
  float: left;
  margin-left: -1.875rem;
}
.scrolling-words-box ul {
  margin: 0 1rem;
  padding: 0;
  animation: scrollUp 40s;
}
.scrolling-words-box ul li {
  opacity: 1;
  align-items: center;
  justify-content: flex-start;
  display: flex;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  list-style: none;
}

@keyframes scrollUp {
  2.5%, 12.5% {
    transform: translateY(-12.5%);
  }
  15%, 25% {
    transform: translateY(-25%);
  }
  27.5%, 37.5% {
    transform: translateY(-37.5%);
  }
  40%, 50% {
    transform: translateY(-50%);
  }
  52.5%, 62.5% {
    transform: translateY(-62.5%);
  }
  65%, 75% {
    transform: translateY(-75%);
  }
  77.5%, 87.5% {
    transform: translateY(-87.5%);
  }
}
