@use "../util/functions" as *;
@use "../globals/fonts" as *;

.tagembed-widget {
    padding-bottom: rem(100);
    padding-top: rem(100);
    position: relative;
    display: flex;
    flex-direction: column;
}

#socials-insta-intro {
    position: absolute;
    text-align: center;
    font-family: 'Digitalt';
    font-weight: 300;
    font-size: rem(70);
    left: 50%;
    transform: translateX(-50%);
    top: 0.5%;
    white-space: nowrap;
    span {
        position: relative;
        display: inline-block;
        color: black;
    }
    span:hover {
        text-decoration: underline;
        color: #131947;
    }
}

#Our-latest-project-banner-socials {
    position: absolute;
    bottom: 0.1%; 
    left: 1%;
    color: rgba(0, 0, 0, 0.77);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: Black Han Sans;
    font-size: 64px;
    font-style: normal;
    font-weight: 600; 
}